.product-card__paddings{
	padding: rem(42px) 15px;
}
.product-card__top{
	.sect-title-wrap{
		margin-bottom: rem(13px);
	}
}
.product-card__top-name{
	font-family: $avenir-med;
	font-size: rem(22px);
}
.product-card__top-description{
	font-size: rem(18px);
}
.product-card__top-socials{
	margin-bottom: rem(10px);
}
.socials-toggle{
	margin-bottom: 5px;
	display: inline-block;
	position: relative;
	font-size: rem(16px);
	color: #979797;
	font-family: $avenir-med;
	text-transform: uppercase;
	&:hover, &:active, &:focus{
		text-decoration: none;
		color: #979797;
	}
	

	&:after{
		content: '+';
		font-size: rem(16px);
		margin-left: rem(10px);
	}
}
.socials-toggle--open{
	&:after{
		content: '-';
		
	}
}
.socials-list{
	display: none;
	font-size: 0;
	li{
		margin: 2px;
		display: inline-block;
	}
}
.socials-link{
	display: block;
}
.ico-fb{
	@include sprite($ico-fb);
}
.ico-vk{
	@include sprite($ico-vk);
}
.ico-twi{
	@include sprite($ico-twi);
}
.ico-inst{
	@include sprite($ico-inst);
}
.ico-pint{
	@include sprite($ico-pint);
}
.ico-g{
	@include sprite($ico-g);
}
.ico-em{
	@include sprite($ico-em);
}
.product-card__top-material{
	margin-bottom: rem(10px);
	color: $text-gray;
	font-size: rem(18px);

	span{
		color: #000;
		text-transform: uppercase;
	}
}
.product-card__top-color{
	margin-bottom: rem(10px);
	font-size: rem(18px);
	span{
		color: #000;
		text-transform: uppercase;
	}
	select{
		display: inline-block;
		margin-left: 10px;
	}
}
.product-card__top-article{
	font-size: rem(18px);
	text-transform: uppercase;
	.article-number{
		color: $text-gray;
		margin-left: 5px;
	}
}
.product-card__top-prices{
	margin: rem(35px) 0;
	font-family: $avenir-med;
	font-size: rem(28px);
}
.product-card__top-ordinary, .product-card__top-sale{
	display: inline-block;
}
.product-card__top-sale {
	margin-left: rem(36px);
	color: $magenta;
}
.ordinary-destroy{
	text-decoration: line-through;
}
.product-card__top-size{
	margin-bottom: rem(14px);
}
.size-title{
	margin-bottom: rem(18px);
	font-size: rem(20px);
	line-height: rem(22px);
	letter-spacing: 2.8px;
	text-transform: uppercase;
}
.size-number{
	font-size: wrem(18px);
}
.size-table-link{
	display: inline-block;
	margin-left: rem(20px);
	vertical-align: middle;
	color: #a6a6a6;
	text-decoration: underline;
	&:hover, &:active, &:focus{
		color: #a6a6a6;
	}
	
}
.product-card__top-quantity{
	margin-bottom: rem(14px);
	font-family: $avenir-med;
	.gray{
		font-size: rem(18px);
		padding-left: 19px;
		padding-right: 10px;
		display: inline-block;
		vertical-align: middle;
		background-color: $bg-gray;
	}
}
.last-item{
	margin-left: rem(20px);
	color: $magenta;
}
.product-card__top{
	.button-green{
		width: 359px;
	}
}
/*-------------custom sekect------------*/
//COLOR
.ik_select.color{
	width: auto !important;
	padding-right: 31px;
	
}
.color-link .ik_select_link_text{
	color: $text-gray !important;
}
.color-link .ik_select_link_text:after{
	top: 50%;
	margin-top: -9px;
}
.color-dropdown {
}
.color-dropdown .ik_select_option_label{
	color: $text-gray;

	&:active{
		text-decoration: underline;
	}
}

//SIZE
.ik_select.size{
	width: auto !important;
	display: inline-block;
	vertical-align: middle !important;
	margin-left: 0;
	padding-left: 24px;
	padding-right: 40px;
	padding-top: 10px;
	padding-bottom: 6px;
	background-color: $bg-gray;
}
.size-link .ik_select_link_text:after{
	top: 50%;
	right: 9px;
	margin-top: -5px;
	transform: rotate(0deg);
	@include sprite($ico-chevron-down-small);
}

.size-link .ik_select_link_text.size-opened{
	&:after{
	transform: rotate(180deg);
		
	}
}

//quantity
.ik_select.quantity{
	width: auto !important;
	display: inline-block;
	vertical-align: middle !important;
	position: relative;
	margin-left: 0;
	padding-left: 24px;
	padding-right: 40px;
	padding-top: 10px;
	padding-bottom: 6px;
	background-color: $bg-gray;
}
.quantity-link .ik_select_link_text:after{
	top: 50%;
	right: 9px;
	margin-top: -5px;
	transform: rotate(0deg);
	@include sprite($ico-chevron-down-small);
}
.quantity-link .ik_select_link_text.quantity-opened{
	&:after{
	transform: rotate(180deg);
		
	}
}

/*-------------end custom sekect------------*/

.breadcrumbs{
	margin-left: rem(-9px);
	margin-top: -30px;
	margin-bottom: rem(21px);
	font-family: $avenir-med;
	font-size:0 ;
	li{
		position: relative;
		display: inline-block;
		margin-left: rem(9px);

		&:after{
			content: '>';
			position: relative;
			display: inline-block;
			margin-left: rem(9px);
			font-size: rem(16px);
		}
		
		a{
			font-size: rem(16px);
			color: #000;
		}
	}
	.breadcrumbs-active{
		a{
			color: #999999;
			&:hover, &:active, &:focus{
				text-decoration: none;
				color: #999999;
			}
			
		}
		&:after{
			display: none;
		}
	}
}

/*------------------carousel------------------*/
#designer-products, #similar-products{
	.owl-prev, .owl-next{
		position: absolute;
		top: 50%;
		margin-top: -70px;
	}
	.owl-prev{
		left: -80px;
		@include sprite($ico-prev);
	}
	.owl-next{
		right: -80px;
		@include sprite($ico-next);
	}
}
/*------------------end of carousel------------------*/
.product-card__prod-slider{
	padding-bottom: 0;
}
.product-card__slider-item{
	text-align: center;
	font-family: $avenir-med;
}
.product-card__slider-img-wrap{
	margin-bottom: rem(15px);
}
.product-card__slider-title{
	font-size: rem(15px);
	color: #000;
}
.product-card__slider-model,{
	color: rgba(#000, .7);
	font-size: rem(14px);
}
.product-card__slider-price{
	font-size: rem(17px);
	color: rgba(#000, .52);
}

/*--------------------VISUAL--------------------*/
.product-card__visual{
	@include clearfix;
	margin-bottom: rem(40px);
}
.product-card__visual-list, .product-card__visual-display{
	float: left;
}
.product-card__visual-list{
	font-size: 0;
}
.product-card__thumb{
	margin-bottom: 9px;
	img{
		width: 100%;
	}
}
.product-card__visual-display{
	width: 427px;
	margin-left: 13px;

	img{
		width: 100%;
	}
}
.product-card__video-play{
		position: relative;
		display: block;
		width: 85px;
		height: 56px;
		border: 4px solid #000;

		&:before{
			content: '';
			@include sprite($ico-play);
			@include absolute-center;

		}
		
	}
.product-card__visual-bottom{
	padding-top: 14px;
	text-align: right;
	clear: both;
}
.product-card__visual-methods{
	display: inline-block;
	position: relative;
	margin-right: rem(20px);
	color: #7d7d7d;
	font-size: rem(18px);
	vertical-align: middle;
	
	&:before{
		content: '';
		vertical-align: middle;
		position: relative;
		margin-right: 10px;
		display: inline-block;
		@include sprite($ico-info);

	}
	
}