.order-confirmation{
		.prod-color,
	.prod-size,
	.prod-quantity{
		&:after{
			display: none;
		}
	}
	.prod-size,
	.prod-quantity{
		//margin-left: rem(20px);
	}
	.price-ordinary,
.price-sale{
	display: block;
		font-size: rem(18px);
	}
	.price-sale{
		margin-left: 0;
	}
	.cart__item-features{
		float: left;
	}
	.cart__item-price{
		float: right;
	}
	.cart__item-title{
		margin-top: rem(11px);
	}
	.cart__item{
		.price-sale{
			display: none;
		}
	}
	.cart__item--sale{
		.price-sale{
			display: block;
		}
	}
}
.order-confirmation__tip-header{
	@extend .cart__tip-header;
}
.order-confirmation__tip-body{
	@extend .cart__tip-body;
	display: block;
}
.ico-payment{
	@include sprite($ico-payment);
}
.order-confirmation__change{
	display: inline-block;
	margin-top: rem(25px);
	margin-bottom: rem(38px);
	color: rgba(#000, .5);
	text-decoration: underline;

	&:hover, &:active, &:focus{
		color: rgba(#000, .5);
	}
	
}
.order-confirmation__delivery-info{
	padding-top: 40px;
	h3{
		margin-bottom: rem(15px);
		font-size: rem(20px);
				text-align: center;

	}
}
.order-confirmation__data{
	margin-bottom: rem(38px);
	font-size: rem(18px);
	color: #666666;
	text-align: center;
}
.order-confirmation__bottom{
	padding-top: rem(38px);

	.cart__final{
		margin-top: rem(26px);
	}
}
.order-confirmation__bottom-right{
	text-align: right;
	label{
		display: block;
		span{
			font-size: rem(18px);
			text-transform: none;
			margin-right: rem(10px);
			color: #666666;
		}
		a{
			color: #666666;
			text-decoration: underline;
		}
	}
	.button-green{
		display: inline-block;
		margin-top: rem(26px);
	}

}