.footer{
	padding: 30px 0;
	background-color: $footer-bg;
	font-size: 14px;
}
.footer__buttons{
	margin-bottom: 20px;
	text-align: center;
}
.footer__button{
	display: inline-block;
	margin: 15px 34px;
	vertical-align: top;
	width: 224px;
	padding: 10px 0;
	font-family: $avenir-med;
	font-size: 14px;
	text-align: center;
	text-transform: uppercase;
	border: 3px solid #fff;
	color: #000;

	&:hover, &:active, &:focus{
		color: #000;
	}
	
}
.footer__inner{
	padding: 10px 0;
	background-color: #fff;

	ul{
		//display: inline-block;
	}
	li{
		text-align: left;
		margin-bottom: 15px;
		a{
			text-align: left;
			color: #000;
		}
	}
		
}

.footer__title{
	margin-bottom: 25px;
	font-family: $avenir-med;
	font-size: 14px;
	font-weight: normal;
	text-transform: uppercase;
	text-align: left;
}
.footer__bottom{
	padding-top: 28px;
	
}
.creators{
	text-align: right;
	a{
		color: #000;
		text-decoration: underline;
	}
}
.footer__socials{
	margin-left: -28px;
	li{
		display: inline-block;
	}
}
.footer__socials-link{
	display: inline-block;
	margin-left: 28px;
}
.footer-twi{
	@include sprite($ico-footer-twi);
}
.footer-fb{
	@include sprite($ico-footer-fb);
}
.footer-vk{
	@include sprite($ico-footer-vk);
}
.footer-inst{
	@include sprite($ico-footer-inst);
}