.delivery__form{
	span{
		display: inline-block;
		margin-bottom: rem(10px);
		font-size: rem(18px);
	}
	label, input{
		display: block;
		width: 100%;
	}
	label{
		margin-bottom: rem(24px);
	}
	input{
		padding-top: rem(11px);
			padding-bottom: rem(10px);
		width: 100%;
		
	}
	input[type="checkbox"]{
		width: auto;
		display: inline-block;
	}
	.delivery__form-personal{
		margin-left: rem(20px);
		font-size: rem(18px);
		color: rgba(#000, .54);
		text-transform: none;
	}
}
.delivery__form-groupe{
	margin-bottom: rem(42px);
}
.delivery__form-title{
	@include clearfix;
}
.delivery__form-ico{
	margin-top: rem(5px);
	@include sprite($ico-courier);
	float: left;
}
.delivery__form-name{
	float: left;
	margin-left: rem(27px);
	font-size: rem(28px);
}
.delivery__form-change{
	float: right;
	margin-top: rem(6px);
	text-decoration: underline;
	color: rgba(#000, .35);
	font-size: 16px;
	font-family: $avenir-med;
	&:hover, &:active, &:focus{
		color: rgba(#000, .35);
	}
	
}
.delivery__form-descr{
	font-size: 16px;

		margin-bottom: 34px;

}
.delivery__form-groupe--mail-delivery{
	.delivery__form-ico{
		@include sprite($ico-delivery-mail);
	}
}