			font-family: $avenir-med;
			font-size: rem(18px);
			line-height: rem(20px);
			text-transform: uppercase;
			text-decoration: none;
			color: #000;

			&:hover, &:active, &:focus{
				color: #000;
				text-decoration: underline;
			}
			// &:before{
			// 	content: '';
