}
.continue-chevron{
	display: inline-block;
	vertical-align: middle;
	margin-top: -3px;
	width: 27px;
				height: 31px;
				background: url(../img/ico-chevron-left.png) 0 0 no-repeat;
}
.cart__item{
	position: relative;
	padding: 22px 0;
	border-top: 2px solid #000;
	//border-bottom: 2px solid #000;
	@include clearfix;

	.price-sale{
		display: none;
	}

	&:last-child{
		border-bottom: 2px solid #000;
	}

	
}
.cart__item--sale{
	.price-ordinary{
		
		text-decoration: line-through;
	}
	.price-sale{
		display: inline-block;
	}
}
.cart__item-close{
	position: absolute;
	display: inline-block;
	@include sprite($ico-close);
	top: 0;
	right: 0;
}
.cart__img-wrap{
	float: left;
	width: 95px;

	img{
		width: 100%;
	}
}
.cart__item-info{
	margin-left: 120px;
}

.prod-color,
.prod-size,
.prod-quantity{
	display: inline-block;
	margin-left: rem(25px);
	position: relative;
	
	span:last-child{
		margin-left: 10px;
	}
	
}
.prod-color,
.prod-size{
	
	&:after{
		content: '';
		display: inline-block;
		vertical-align: middle;
		margin-left: rem(25px);
		width: 1px;
		height: 18px;
		background-color: #DBC1C1;
	}
}
.prod-color{
	margin-left: 0;
}
.cart__item-title{
