/*--------------------checks----------------------*/
.cart__checkboxes{
	font-family: $avenir-med;

	input[type="radio"]{
		position: absolute;
		left: -9999px;
		display: inline-block;
		margin-bottom: 0;
		vertical-align: middle;
		border: 1px solid #000;

		&:checked + .cart__checkboxes-custom .cart__checkboxes-dot{
			display: block;
		}
	}
}
.cart__checkboxes-custom{
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 15px;
	height: 15px;
	border-radius: 50%;
	border: 1px solid #000;
	.cart__checkboxes-dot{
		display: none;
		position: absolute;
		width: 7px;
		height: 7px;
		background-color: #000;
		@include absolute-center;
		border-radius: 50%;
	}

}

.cart__checkboxes-item{
	margin-bottom: rem(50px);
}
.cart__checkboxes-header{
	position: relative;
	padding-top: rem(15px);
	padding-bottom: rem(9px);
	margin-bottom: rem(45px);
		background-color: #000;

		&:before{
			content: '';
			position: absolute;
			display: block;
			height: 100%;
			width: 100%;
			background-color: #000;
			top: 0;
			left: -100%;
		}

}



.ico-box-invert{
	@include sprite($ico-box-invert);
}
.cart__checkboxes-ico,
.cart__checkboxes-title{
	display: inline-block;
	vertical-align: middle;
}
.cart__checkboxes-title{
	margin-left: rem(24px);
	font-family: $avenir-med;
	font-size: rem(24px);
	color: #fff;
	text-transform: uppercase;
}
.cart__checkboxes-body{
	@include clearfix;
	label{
		position: relative;
		display: block;
		margin-bottom: rem(29px);

	}
}
.cart__checkboxes-text{
	display: block;
}
.cart__checkboxes-method{
	display: inline-block;
	vertical-align: middle;
	margin-left: rem(25px);
	font-size: rem(19px);
}
.cart__checkboxes-text{
	margin-top: rem(8px);
	font-size: rem(16px);
	color: #999999;
	text-transform: none;
}
.ico-payment-invert{
	@include sprite($ico-payment-invert);
}
.ico-promo-invert{
	@include sprite($ico-promo-invert);
}
.label-promo{
	float: left;
		width: 60%;
	input{
		float: left;
	}
}
.cart__checkboxes-send{
	width: 35%;
	float: right;
	padding-top: rem(15px);
			padding-bottom: rem(15px);
	border: 2px solid #E6E6E6;
	font-size: rem(18px);
	text-transform: uppercase;
	background-color: transparent;
}
/*--------------------end of checks----------------------*/

/*--------------------tips----------------------*/

.cart__tip{
	margin-bottom: 9px;
	padding: rem(30px) 0 rem(5px) rem(52px);
	background-color: $tips-bg;

	span{
		display: block;
		margin-top: rem(18px);
	}
}
.cart__tip-header{
	display: block;
	padding-right: rem(20px);
	margin-bottom: rem(25px);
	@include clearfix;
	color: #000;

	&:hover, &:active, &:focus{
		text-decoration: none;
		color: #000;
	}
	
}

.cart__tip-ico{
	float: left;
}
.ico-locked{
	@include sprite($ico-locked);
}
.cart__tip-title{
	float: left;
	margin-left: rem(22px);
	font-family: $avenir-med;
	font-size: rem(20px);
	line-height: rem(22px);
	text-transform: uppercase;
}
.cart__tip-chevron{
	float: right;
	margin-top: rem(7px);
	@include sprite($ico-slidedown-black);
}
.cart__tip-body{
	display: none;
	margin-bottom: rem(47px);
	padding-right: rem(82px);
	line-height: 1.4;
}
.chevron-up{
	transform: rotate(180deg);
}
.ico-box{
	@include sprite($ico-box);
}
.cart__tip-link{
	color: #3c3c3c;
	text-decoration: underline;

}
.ico-return{
	@include sprite($ico-return);
}
.ico-contact{
	@include sprite($ico-contact);
}
/*--------------------end of tips----------------------*/

