.enter{
	
}
.enter__form-author{
	font-size: rem(18px);
	label{
		display: block;
		margin-bottom: rem(12px);
		width: 100%;
		@include clearfix;
	}
	span{
		float: left;
		
		text-transform: uppercase;
		padding: rem(17px) 0;
	}
	input{
		width: 353px;
		float: right;
		
		padding-top: rem(11px);
			padding-bottom: rem(10px);
		background-color: $footer-bg;

		@include placecolor(#999999);
	}
	.enter__form-bottom{
		@include clearfix;
		margin-top: rem(30)px;
	}
	.button-green{
		width: 276px;
		padding-left: 20px;
		float: right;
		position: relative;

		
		&:after{
			content: '';
			position: absolute;
			display: block;
			left: 72px;
			top: 50%;
			margin-top: -15px;
			@include sprite($ico-enter-lock);
		}
	}
	.enter__form__forget{
		float: left;
		display: inline-block;
		padding: rem(17px) 0;
		text-decoration: underline;
		color: #999999;
	}
}
.enter__form-reg{
	font-size: rem(18px);
	input{
		padding-top: rem(11px);
			padding-bottom: rem(10px);
		width: 100%;
		
	}
	.button-green{
		width: 100%;
	}
	.info-about{
		color: #999999;
		letter-spacing: -.5px;
		margin-bottom: rem(26px);
	}

}
.row-with-line{
	position: relative;
	&:before{
		content: '';
		position: absolute;
		top: 0;
		left: 54%;
		display: block;
		height: 100%;
		width: 3px;
		background-color: #000;

	}
	
}