@import "bourbon/_bourbon";
/*==========  Desktop First Method  ==========*/

 

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px){
	.header__search{
		float: right;
		margin-top: 5px;
	}
	.header__info{
		//width: 100%;
		//display: table;
	}
	.header__info-left{
		float: right;
		//display: table-footer-group;
	}
	
	.cart__tip-title{
		font-size: rem(17px);
	}
	.product-card__visual-list{
		text-align: center;
		float: none;
	}
	.product-card__thumb, .product-card__video-play{
		display: inline-block;
		vertical-align: top;
		margin-left: .5%;
		margin-right: .5%;
	}
	.product-card__video-play{
		margin-bottom: 9px;
	}

}
	
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px){
	.header__info-left{
		float: left;
		//display: table-footer-group;
	}
	.header__info-right{
		position: absolute;
		top: 0;
		right: 0;
	}
	.footer__inner-item{
	display: inline-block;
	}
	.footer__socials{
		text-align: right;
		margin-bottom: 15px;
	}
	.contacts__formwrapper{
		padding-left: 15px;
		padding-right: 15px;
	}
	.cart__result,
	.cart__final.green{
		margin-bottom: rem(15px);
	}
	.product-card__visual-bottom{
		text-align: center;
	}
	.product-card__visual-display{
		float: none;
		margin: 0 auto;
	}
	.product-card__visual-methods{
		margin-right: 0;
	}
	.enter__form-author{

		span{
			float: left;
			//display: block;

		}
		input{
			width: 72%;
			float: right;
			
		}
	}
	.enter__form-author .button-green{
		width: 198px;
		padding-left: 9px;
		&:after{
			left: 29px;
		}
	}
	.enter__form-title--author{
		margin-bottom: rem(20px);
	}
	.enter__form-reg .info-about{
		line-height: rem(26px);
	}
}
	

/* Small Devices, Tablets */
@media only screen and (max-width : 768px){
	html{
		font-size: 12px;
	}
	.header__info-right{
		position: static;
		float: left;
	}
	.header__info{
		margin-left: 0;
	}
	.header__top-item{
		margin-bottom: 15px;
	}
	.header__search{
		position: absolute;
		top: 5px;
		right: 0;
	}
	.footer__inner-item{
		margin-bottom: 30px;
		text-align: center;
	}
	.footer__inner{
		.footer__inner-item:last-child{
			margin-bottom: 0;
		}
	}
	.footer__inner{
		li{
			text-align: center;
		}
	}
	.footer__title{
		text-align: center;
	}
	.row-with-line{
		&:before{
			display: none;
		}
	}
	.enter__form-author{
		margin-bottom: 60px;
		.enter__form__forget{
			display: block;
			float: none;
		}

	}
	.enter{

		button.button-green{
			float: none;
			width: 100%;
			margin: 0 auto;
		}
	}
	.formwrapper{
		padding-left: rem(30px);
		padding-right: rem(30px);
	}

	/*Disable Animation on Mobile Devices*/
	.animated{
		
		/*CSS transitions*/
		-o-transition-property: none !important;
		-moz-transition-property: none !important;
		-ms-transition-property: none !important;
		-webkit-transition-property: none !important;
		transition-property: none !important;
		/*CSS transforms*/
		-o-transform: none !important;
		-moz-transform: none !important;
		-ms-transform: none !important;
		-webkit-transform: none !important;
		transform: none !important;
		/*CSS animations*/
		-webkit-animation: none !important;
		-moz-animation: none !important;
		-o-animation: none !important;
		-ms-animation: none !important;
		animation: none !important;
	}
	.header__logo-wrap{
		text-align: center;
	width: 100%;
	max-width: initial;


	}
	.header__info{
		text-align: center;
	}
	.header__info-left{
		float: none;
	}
	.header__info-right{
		float: none;
		padding-top: 20px;
	}
	.header__top-item{
		&:first-child{
			margin-left: 0;
		}
	}
	
	.cart{
		.continue-ordering{
			max-width: 351px;
			
		}
	}
	.order-confirmation{
		.price-ordinary,
.price-sale{
	font-size: rem(30px);
	}
}
	.enter__form-author{

			span{
				float: none;
				display: block;

			}
			input{
				width: 100%;
				float: none;
				
			}
		}
	.enter__form-reg .info-about{
		margin-bottom: rem(205px);
	}
	.product-card__visual-list{
		text-align: center;
		float: left;
	}
	.product-card__thumb, .product-card__video-play{
		display: block;
		vertical-align: top;
	}
	
	.enter__form-author .button-green{
		width: 100%;
		padding-left: 20px;
		&:after{
			left: 72px;
		}
	}
	.header__search{
		top: 0;
	}

}


@media only screen and (max-width : 767px){	
	.enter__form-reg .info-about{
		margin-bottom: rem(26px);;
	}
}
@media only screen and (max-width : 650px){
	.product-card__visual-list{
		//text-align: center;
		float: none;
	}
	.product-card__thumb, .product-card__video-play{
		display: inline-block;
		vertical-align: top;
	}
	.product-card__video-play{
		margin-bottom: 9px;
	}
}
@media only screen and (max-width : 550px){
	.prod-color, .prod-size,  .prod-quantity{
		margin: 0;
		display: block;
		&:after{
			display: none;

		}

	}
}
	

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px){
	html{
		font-size: 11px;
	}
	.header__search{
		position: static;
		float: left;
		margin-left: 20px;
		top: 5px;
		right: 0;
	}
	.copyright{
		text-align: center;
		margin-bottom: 15px;
	}
	.footer__socials, .creators{
		text-align: center;
		
	}
	
	.contacts__form{
		.button-green{
			width: 100%;
		}
	}
	.contacts__form .contacts__form-personal{
	font-size: 10px;
	}
	.delivery__form-title{
		text-align: center;
	}
	.delivery__form-ico, .delivery__form-name, .delivery__form-change{
		float: none;
		display: inline-block;
	}
	.delivery__form-name, .delivery__form-change{
		width: 100%;
		display: block;
		margin-left: 0;
		text-align: center;
	}
	.continue-ordering{
		width: 100%;
		float: none;
	}
	.delivery__form-personal{
		width: 80%;
		vertical-align: middle;

	}
	.cart .sect-title-wrap .continue{
		font-size: 1rem;
	}
	.cart__item-info{
		padding-right: 50px;
	}
	.price-sale{
		margin-left: 0;
	}
	.order-confirmation{
		.cart__item-info{
			padding-right: 0;
		}
		.cart__item-features{
			float: none;
		}
		.cart__item-price{
			float: none;
		}
		.price-ordinary,
		.price-sale{
			display: inline-block;
			font-size: rem(18px);
		}
		.price-sale{
			margin-left: 10px;
		}
		.cart__item--sale{
		.price-sale{
			display: inline-block;
		}
	}
	}
	.product-card__slider-title{
		font-size: rem(30px);
		color: #000;
	}
	.product-card__slider-model,{
		color: rgba(#000, .7);
		font-size: rem(25px);
	}
	.product-card__slider-price{
		font-size: rem(35px);
		color: rgba(#000, .52);
	}
	.product-card__top .button-green {
		width: 80%;
		margin: 0 auto;
	}
	.product-card__visual-list{
		//text-align: center;
		float: none;
	}
	.product-card__thumb{
		display: inline-block;
		width: 15%;
	}
	.product-card__visual-display{
		width: 90%;
	}
	.order-confirmation__bottom-right{
		text-align: right;
		label{
			display: block;
			span{
				width: 70%;
			}
			a{
				color: #666666;
				text-decoration: underline;
			}
			input[type="checkbox"]{
				display: inline-block;
				vertical-align: middle;
				
			}
		}

	}
}
		

/* Custom, iPhone Retina */
@media only screen and (max-width : 320px){
	.cart__item-features{
		margin-bottom: 10px;
	}
	.last-item{
		display: block;
		margin-left: 0;
	}
	.label-promo,
	.cart__checkboxes-send{
		float: none;
		display: block;
	}
}


