input{
	outline: none;
}
a{
	outline: none !important;
}
section{
	padding-top: rem(42px);
	padding-bottom: rem(45px);
	
	.gray{
		background: $bg-gray;
	}
	.sect-title-wrap{

		margin-bottom: rem(50px);
	}
	h1{
		display: inline-block;
		margin: 0;
		font-weight: normal;
		font-family: $avenir-med;
		padding-bottom: 3px;
		border-bottom: 2px solid #000;
		text-transform: uppercase;
		font-size: rem(30px);
		color: #000;
		text-transform: uppercase;
	}
	h2{
		margin-top: 0;
		margin-bottom: rem(30px);
		font-family: $avenir-med;
		font-weight: normal;
		font-size: rem(24px);
		color: #000;
		text-transform: uppercase;
		text-align: center;
	}
	h3{
		margin-top: 0;
		margin-bottom: rem(30px);
		font-family: $avenir-med;
		font-weight: normal;
		font-size: rem(22px);
		text-transform: uppercase;
		text-align: left;
		color: #000;
	}
	form{

		input{
			margin-bottom: rem(15px);
			padding-top: rem(15px);
			padding-bottom: rem(15px);
			padding-left: rem(20px);
			padding-right: rem(20px);
			font-size: rem(18px);
			border: none;
			
			background-color: $footer-bg;

			@include placecolor(#999999);
		}
		label{
			span{
				text-transform: uppercase;
				span{
					font-family: $avenir-med;
					color: #aeaeae;
				}
			}
		}
	}
}
.sect-title-wrap--center{
	text-align: center;
}
.sect-main{
	padding-top: 0;
}
.formwrapper{
	padding: rem(40px) 0;
	border: 3px solid #000;
}
ul, li{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.form-graytext{
	text-transform: none;
	margin-bottom: 0 !important;
}
.cart__tip .tip-selection {
	display: inline-block;
	margin-top: 0;
	color: #3c3c3c;
}
.button-green{
	display: block;
	padding: rem(17px) 0;
	font-size: rem(22px);
	background-color: $accent;
	color: #000;
	text-transform: uppercase;
	border: 3px solid #000;

	&:active{
		background-color: $accent-hover;
	}
	
}
.continue-ordering{
	width: 352px;
}
.info-button{
	display: inline-block;
	margin-top: 3px;
	vertical-align: top;
	@include sprite($ico-info);
}
.horizontal-line{
	height: 2px;
	background-color: #000;
}
.ruble{
	font-family: $rubl;
}
.toggle-mnu{

  display: block;
  width: 28px;
  float: left;
  height: 28px;
  margin-top: 5px;

  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: 9px;
  } 

  span:after{

    top: 18px;
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px;
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(-1px, 0px);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(6px, -7px);
  } 
} 

/*-----------------mmmenu---------------*/
