.header{
	margin-bottom: rem(60px);
}
.header__top{
	margin-bottom: rem(13px);
	//padding-top: 18px;
}
.header__relative{
	position: relative;
}
.header__static{
	position: static;
}
.header__info-left{
	float: left;
}
.header__info-right{
	float: right;
}
.header__logo-wrap{
	max-width: 215px;
	display: inline-block;
	padding-top: 18px;
}
.header__info-right{
	padding-top: 30px;
	padding-bottom: 23px;
	padding-left: 8px;
	padding-right: 3px;
	background-color: $bg-gray;
}
.header__info{
	@include clearfix;
	margin-left: -22px;
	text-transform: uppercase;
	font-family: $avenir-med;
	font-size: 11px;
	line-height: 17px;
}
.header__info-left{
	padding-top: 30px;
}
.header__top-item{
	display: inline-block;
	margin-left: 22px;
}
.header__top-ico, .header__top-text{
	display: inline-block;
	vertical-align: middle;
	
}
.header__top-text{
	margin-left: 5px;
}
.top-phone{
	@include sprite($ico-phone);
}
.top-clock{
	@include sprite($ico-clock);
}
.top-mail{
	@include sprite($ico-mail);
}
.top-user{
	@include sprite($ico-user);
}
.top-cart{
	@include sprite($ico-cart);
}
.header__reg-ico, .header__reg-links{
	display: inline-block;

}
.header__reg-links{
	a{
		color: #000;
	}
}
.header__enter, .header__cart{
	display: inline-block;
}
.header__cart{
	margin-left: 10px;
}
.green{
	background-color: #C3FD8B;
}

.header__bottom{
	background-color: #000;

	nav{
		display: inline-block;
	}

}
.header__bottom--abs{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 9999;
}
.header__search{
		display: inline-block;

	.header__search-inner{
		position: relative;
	}
		input[type="text"]{
			width: 116px;
			padding-left: 30px;
			position: relative;
			border-radius: 12px;
	
	background-color: #fff;
	border: none;
		}
		input[type="submit"]{
			position: absolute;
			left: 9px;
			top: 5px;
			font-size: 0;
			@include sprite($ico-loupe);
			background-repeat: no-repeat;
			border: none;
			background-color: transparent;
		}
}

.header__bottom-descr{
	padding: 9px 0;
	font-size: 12px;
	text-transform: uppercase;
	color: #fff;
	
		
}
.header-menu{
	font-size: 0;
}
.header-menu__item{
	display: inline-block;

	&--active{
		.header-menu__link{
			background-color: #fff;
			text-decoration: none;
			color: #000;
		}
	}
}
.header-menu__link{
	display: block;
	padding: 9px 15px;
	font-size: 12px;
	text-transform: uppercase;
	color: #fff;
	//border-right: 1px solid #fff;
	border-left: 1px solid #fff;

	&:hover, &:active, &:focus{
		background-color: #fff;
			text-decoration: none;
			color: #000;
	}
	
}