.contacts__phones, .contacts__mail, .contacts__offer{
	text-align: center;
}
.contacts__phones{
	margin-bottom: rem(40px);
	font-family: $avenir-med;
	font-size: rem(34px);
	color: #000;
}
.contacts__mail{
	margin-bottom: rem(30px);
	font-size: rem(30px);
}
.contacts__offer{
	margin-bottom: rem(55px);
	font-size: rem(22px);
	color: #3c3c3c;
}
.contacts__formtitle {
	margin-bottom: rem(40px);
	font-size: rem(30px);
	text-transform: uppercase;
	font-family: $avenir-med;
	color: #000;
	font-weight: normal;
}

.contacts__form{
	label, input{
		display: block;
		width: 100%;
	}
	label{
		margin-bottom: rem(28px);
	}
	span{
		display: inline-block;
		margin-bottom: rem(5px);
		font-size: rem(26px);
	}
	textarea{
		width: 100%;
		padding: 15px;
		border: none;
		background-color: $footer-bg;
	}
	input[type="checkbox"]{
		display: inline-block;
		width: auto;
	}
	.contacts__form-personal{
		display: inline-block;
		width: auto;
		margin-left: 20px;
		font-size: rem(20px);
		text-transform: none;
	}
	.button-green{
		width: 362px;

	}
}